import React, { useMemo } from "react";
import objectPath from "object-path";
import { useHtmlClassService } from "../../layout";
import DashboardHeader from "./Nested/Header";
import { Cards } from "./Nested/Cards";

export function Dashboard() {
    return <>
        <div className="container-fluid">
            <div className="card card-custom gutter-b">
                <DashboardHeader />
            </div>
            <div className="row">
                <Cards path="/media/svg/icons/Design/valid_request.svg" name="Valid Requests" dataValue={0} percent="4.32%" />
                <Cards path="/media/svg/icons/Design/defaulted.svg" name="Defaulted" dataValue={0} percent="1.27%" />
                <Cards path="/media/svg/icons/Design/impressions.svg" name="Impressions" dataValue={0} percent="4.27%" />
                <Cards path="/media/svg/icons/Design/cost.svg" name="Cost" dataValue={0} percent="3.27%" />
                <Cards path="/media/svg/icons/Design/revenue.svg" name="Revenue" dataValue={0} percent="1.28%" />
                <Cards path="/media/svg/icons/Design/ecpm.svg" name="E-CPM" dataValue={0} percent="1.28%" />
            </div>
        </div>
    </>;
}
